@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scrollbar-width: none;
  -ms-overflow-style: none; 
  
}

::-webkit-scrollbar {
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Reset básico para remover margens/paddings padrão */
* {
  padding: 0;
  box-sizing: border-box;
}

/* Se quiser remover margens do body especificamente */
body {
  margin: 0;
  padding: 0;
}

/* Fonte local para o Navbar (exemplo) */
@font-face {
  font-family: 'MinhaFonte';
  src: url(/conviteCasamento/static/media/FontsFree-Net-lawrence-regular.8fcaf569.woff2) format('woff2');
  font-weight: 500;
  font-style: normal;
}

/* Barra de navegação principal */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;

  display: flex;
  align-items: center;
  justify-content: space-between;

  /* Padding para o navbar */
  padding: 0.5rem 1rem;

  background-color: #fff;  /* Mantém navbar branco */
  
  /* Remove a borda inferior e adiciona sombra */
  box-shadow: 0 20px 20px rgba(0, 0, 0, 0.1);
  
  /* Usa a fonte local */
  font-family: 'MinhaFonte', sans-serif;
}

/* Logo à esquerda */
.nav-logo {
  font-size: 2rem;
  color: #333;
  white-space: nowrap;
}

/* Lista de links à direita */
.nav-links {
  list-style: none;
  display: flex;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
}

/* Links */
.nav-links a {
  text-decoration: none;
  color: #333;
  font-weight: 600;
  font-size: 1.2rem;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #666;
}

/* Botão de confirmação de presença */
.confirm-button {
  padding: 0.5rem 1rem;
  border: 2px solid #333;
  border-radius: 4px;
  background-color: #fff;
  color: #333;
  font-size: 1.2rem;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.confirm-button:hover {
  background-color: #333;
  color: #fff;
}

/* Fonte local para o Header */
@font-face {
    font-family: 'Lawrence';
    src: url(/conviteCasamento/static/media/FontsFree-Net-lawrence-regular.8fcaf569.woff2) format('woff2');
    font-weight: normal;
    font-style: normal;
  }
  
  /* Container do header */
  .header {
    /* Atualiza a cor de fundo para #FFFCF8 */
    background-color: #FFFCF8;
  
    /* Imagem de fundo */
    background-image: url(/conviteCasamento/static/media/background.6fc22055.jpg);
    background-repeat: no-repeat;
    background-size: cover;      /* Para preencher todo o espaço (corta um pouco se necessário) */
    background-position: top center; 
  
    /* Aumenta o height do header alterando a min-height para 120vh (ajuste conforme desejado) */
    min-height: 70vh;
  
    /* Remove padding-top para que a imagem inicie no topo */
    padding-top: 0;
  
    /* Centraliza qualquer conteúdo direto no .header */
    text-align: center;
    position: relative;
  }
  
  .hero {
    padding-top: 250px; 
  
    font-family: 'Lawrence', sans-serif;
    color: #274D66;
    max-width: 800px;
    margin: 0 auto;
    text-align: center;
  }
  
  /* Título (Felipe & Esther) */
  .hero h1 {
    font-size: 5.5rem;
    margin: 0;
    margin-bottom: 0.5rem;
    color: #344B68;
  }
  
  /* Subtítulo (data, local, etc.) */
  .hero p {
    margin: 0;
    margin-top: 0.5rem;
    font-size: 1.7rem;
  }
  
.countdown {
  margin-top: 10rem;      /* Espaçamento superior opcional */
  color: #274D66;        /* Cor principal do texto */
}

.countdown-title {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

/* Container que agrupa cada item (dias, horas, minutos) */
.countdown-items {
  display: flex;
  justify-content: center; /* Centraliza horizontalmente */
  grid-gap: 3rem;
  gap: 3rem;              /* Espaço entre cada bloco */
}

/* Cada bloco individual (ex.: "40" + "dias") */
.countdown-item {
  display: flex;
  flex-direction: column; /* Número em cima, texto embaixo */
  align-items: center;
}

/* O número em destaque */
.number {
  font-size: 3rem;
  color: #274D66;
}

/* O rótulo (dias, horas, minutos) */
.label {
  font-size: 1.5rem;
  color: #555;
}

.countdown-section {
    width: 100%;
    background-color: #FFFCF8;  /* Fundo desejado */
    text-align: center;
    padding: 3rem 1rem;         /* Espaçamento interno */
    color: #3F6F90;             /* Cor principal do texto */
    box-sizing: border-box;
    font-family: 'Lawrence', sans-serif;  /* Aplica a fonte importada */
  }
  
  .countdown-section h2 {
    font-size: 2.5rem;
    margin-bottom: 1rem;
  }
  
  .countdown-section p {
    max-width: 900px;           /* Limita a largura do texto */
    margin: 0 auto 1rem;
    line-height: 1.5;
    font-size: 1.5rem;
    color:black; 
  }
  
  .signature {
    font-style: italic;
    margin-bottom: 2rem;
  }
  
  /* Estilo para o componente de contagem (se o CountdownTimer utiliza a classe .countdown) */
  .countdown {
    font-size: 2rem;
    margin-top: 1rem;
    color: #555;
  }
  
/* Importação da fonte "Lawrence" */
@font-face {
  font-family: 'Lawrence';
  src: url(/conviteCasamento/static/media/FontsFree-Net-lawrence-regular.8fcaf569.woff2) format('woff2');
  font-weight: normal;
  font-style: normal;
}

/* Seção geral */
.events {
  background-color: #FFFCF8;
  padding: 3rem 1rem;
  font-family: 'Lawrence';
  text-align: center; /* Centraliza o título */
}

/* Título "Programação" */
/* Para aumentar o tamanho do título, ajuste o valor de font-size abaixo */
.events h2 {
  font-size: 3rem; /* Aumentado de 2.8rem para 3rem */
  margin-bottom: 3rem;
  font-family: 'Lawrence', sans-serif;
  color: #344B68;
}

/* Container para todos os eventos */
.events-container {
  max-width: 1200px; /* Largura máxima do container */
  margin: 0 auto;    /* Centraliza horizontalmente */
}

/* Cada evento */
.event {
  display: flex;
  font-family: 'Lawrence';
  align-items: center;      /* Alinha verticalmente no meio */
  justify-content: center;  /* Alinha horizontalmente no meio */
  grid-gap: 2rem;
  gap: 2rem;                /* Espaço entre texto e mapa */
  margin-bottom: 3rem;      /* Espaço entre eventos */
}

/* A cerimônia (texto-esquerda, mapa-direita) */
.event.normal {
  flex-direction: row;
}

/* Festa Pós Cerimônia (mapa-esquerda, texto-direita) */
.event.reverse {
  flex-direction: row-reverse;
}

/* Texto (informações) */
/* Para aumentar os tamanhos do texto neste bloco, ajuste os valores de font-size em .event-info h3, .event-date e .event-address */
.event-info {
  display: flex;
  flex-direction: column;
  align-items: center; /* Centraliza o bloco de texto */
  text-align: center;  /* Centraliza o texto */
}

/* Mapa + links */
.event-map {
  display: flex;
  flex-direction: column;
  align-items: center; /* Centraliza o iframe e os links */
}

/* Título do evento */
/* Para aumentar o tamanho do título do evento, ajuste o font-size abaixo */
.event-info h3 {
  font-family: 'Great Vibes', cursive;
  font-size: 2.5rem; /* Aumentado de 2rem para 2.5rem */
  margin-bottom: 0.5rem;
  color: #344B68;
}

/* Data e hora */
/* Para aumentar o tamanho da data/hora, ajuste o font-size abaixo */
.event-date {
  font-size: 1.2rem; /* Aumentado de 1.1rem para 1.2rem */
  font-weight: bold;
  margin-bottom: 0.5rem;
}

/* Endereço */
/* Para aumentar o tamanho do endereço, ajuste o font-size neste seletor se necessário */
.event-address {
  font-size: 1.1rem; /* Adicionado para controle do tamanho, ajuste se necessário */
  margin-bottom: 1rem;
  line-height: 1.4;
}

/* Links */
/* Para aumentar o tamanho dos links, ajuste o font-size aqui se desejar */
.event-links {
  margin-top: 1rem;
}

.event-links a {
  font-size: 1.1rem; /* Adicionado para controle do tamanho, ajuste se necessário */
  color: #274D66;
  text-decoration: none;
  font-weight: 600;
  margin: 0 0.5rem;
}

.event-links a:hover {
  text-decoration: underline;
}

