}

body {
  font-family: 'Montserrat', sans-serif, 'serif', 'Playfair Display';
  scrollbar-width: none; /* Oculta a barra de rolagem */
  -ms-overflow-style: none; /* Para o Internet Explorer 10+ */
  color: #333;
  background-color: #fdf8f1; 
  margin: 0;
  padding: 0;
}
body::-webkit-scrollbar {
  display: none;
}

@media (max-width: 768px) {
  .nav-links {
    flex-direction: column;
    grid-gap: 0.5rem;
    gap: 0.5rem;
  }

  .hero-title {
    font-size: 2rem;
  }

  .hero-section::before,
  .hero-section::after {
    width: 80px;
    height: 80px;
  }
}

